#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 85rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}
.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
.skillDesc{
    font-weight: 400;
    font-size: 1 rem;
    max-width: 50rem;
    padding: 0 2rem;
}
.skillBars{
margin: 1.5rem;
width: 100vw;
max-width: 80%; 
text-align: left;
}
.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50,50,50 );
    /* justify-content: center; */
    align-items: center;

}
.skillBarImg{
    object-fit: cover;
    height: 100px;
    width: 10%;
    margin: 1rem;
}
.skillBarText p{
font-size: 0.85rem;
font-weight: 200;
justify-content: center;
text-size-adjust:auto;
max-width: 100%;
}

@media screen and (max-width:720px) {
    .skillBarText>p{
        font-size: 2vw;
    }
}
@media screen and (max-width:480px) {
    .skillBarText>p{
        font-size: 3vw;
    }
    .skillBarImg{
        height: 2.5rem;
        width: 2.5rem;
        margin-right: 0.5rem; 
    }
    .skillBarText>h2{
        font-size: 5vw;
    }
}

